import { Switch, Route, Redirect } from 'react-router-dom';
import Layout from '../components/Layout';
import Solution from '../views/Solution/Index';
import HowToStart from '../views/HowToStart/Index';
import Ecosystem from '../views/Ecosystem/Ecosystem';
import Partners from '../views/Partners/Partners';
import Resources from '../views/Resources/Resources';
import Roadmap from '../views/Roadmap/Roadmap';
import Team from '../views/Team/Team';
import ContactUs from '../views/ContactUs/ContactUs';

const AppRoutes = () => (
  <Layout>
    <Switch>
      <Route path="/" exact>
        <Solution />
      </Route>
      <Route path="/volunteer/:type" exact>
        <HowToStart />
      </Route>
      <Route path="/volunteer" exact>
        <HowToStart />
      </Route>
      <Route path="/ecosystem" exact>
        <Ecosystem />
      </Route>
      <Route path="/partners" exact>
        <Partners />
      </Route>
      <Route path="/reasources" exact>
        <Resources />
      </Route>
      <Route path="/roadmap" exact>
        <Roadmap />
      </Route>
      <Route path="/founders" exact>
        <Team />
      </Route>
      <Route path="/contact" exact>
        <ContactUs />
      </Route>
      <Redirect to="/" />
    </Switch>
  </Layout>
);

