import { useState } from 'react';
import { useLocation, useHistory, Link, NavLink } from 'react-router-dom';
import { Menu, Dropdown } from 'antd';
import classNames from 'classnames';
import { menu } from './constants';
import Logo from '../../assets/images/logo.png';
import Hamburger from '../../assets/images/hamburger.png';
import HamburgerActive from '../../assets/images/hamburger_active.png';
import Style from './Style.module.scss';

const renderMenu = ({ menus, history, location }) => (
  <Menu>
    {menus.map((item) => (
      <Menu.Item key={`subMenu${item.path}`}>
        <div
          onClick={() => history.push(item.path)}
          className={`${Style.HeaderNavItem} ${
            location.pathname === item.path ? Style.MenuActive : ''
          }`}
          key={item.path}>
          {item.name}
        </div>
      </Menu.Item>
    ))}
  </Menu>
);

