const Header = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);

  const location = useLocation();

  const history = useHistory();

  const hideNav = () => setIsMenuOpen(false);

  const isCurrent = (path) => {
    if (path === '/') {
      return location.pathname === '/';
    }

    return location.pathname.includes(path);
  };

  const convertMenu = (oriMenu, onClick, parentKey = '') =>
    oriMenu.map((item, index) => {
      const hasChildren = Array.isArray(item.children) && item.children.length !== 0;
      return {
        label: (
          <NavLink
            exact
            className={(isActive) => classNames(Style.navLink, { [Style.active]: isActive })}
            to={item.path}
            onClick={hasChildren ? () => {} : onClick}>
            {item.name}
          </NavLink>
        ),
        key: `${parentKey} menuItem${index}`,
        children:
          hasChildren && convertMenu(item.children, onClick, `${parentKey} menuItem${index}`),
      };
    });

  const convertedMenu = convertMenu(menu, hideNav, '');

  return (
    <div className={Style.Header}>
      <div className={Style.HeaderContainer}>
        <div className={Style.HeaderLeft} onClick={() => history.push('/')}>
          <img src={Logo} alt="gfu" />
        </div>

        <div className={Style.HeaderCenterNav}>
          {menu.map((item) =>
            item.children ? (
              <Dropdown
                overlay={renderMenu({
                  menus: item.children,
                  history,
                  location,
                })}
                key={`headerSub${item.path}`}
                trigger={['hover']}>
                <div
                  onClick={() => {
                    history.push(item.path);
                  }}
                  className={`${Style.HeaderNavItem} ${
                    isCurrent(item.path) ? Style.MenuActive : ''
                  }`}>
                  {item.name}
                </div>
              </Dropdown>
            ) : (
              <div
                onClick={() => {
                  history.push(item.path);
                }}
                className={`${Style.HeaderNavItem} ${isCurrent(item.path) ? Style.MenuActive : ''}`}
                key={`header${item.path}`}>
                {item.name}
              </div>
            ),
          )}
        </div>

        <div className={Style.HeaderRightNav}>
          <img
            className={Style.Hamburger}
            src={isMenuOpen ? HamburgerActive : Hamburger}
            alt="gfu"
            onClick={() => {
              setIsMenuOpen(!isMenuOpen);
            }}
          />
          {isMenuOpen && (
            <div
              className={Style.HamburgerNav}
              onTouchMove={(e) => {
                e.preventDefault();
              }}>
              {/* {renderFirstMenu({ menus: menu, history, location, hideNav })} */}
              <Menu
                style={{ marginLeft: 'auto', maxWidth: '500px' }}
                items={convertedMenu}
                mode="inline"
                theme="dark"
              />
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Header;
